// external libs
import {FieldDefinition, Valcheck} from 'valcheck/lib/valcheck/Valcheck';
// local libs
import {NodesetTemplate, TemplateFieldType} from '@linkurious/rest-client';

import {NodeChecker} from './NodeChecker';

import {InputSerialization, TemplateCheckerAttributes} from './index';

export class NodeSetChecker<E> extends NodeChecker<E> {
  // TODO NodeSet is not technically a Node because they have different patterns
  constructor(check: Valcheck<E>) {
    super(check);
  }

  public get attributes(): TemplateCheckerAttributes {
    return {
      type: TemplateFieldType.NODE_SET,
      shorthand: 'categories',
      defaultSerializer: InputSerialization.NODE_SET
    };
  }

  /**
   * Template input value field definition.
   *
   * @param options
   */
  protected getInputFieldDefinition(options?: NodesetTemplate['options']): FieldDefinition<E> {
    return {check: ['stringArray', 1]};
  }
}
